// =========================================/
// Global Admin
// =========================================/

.page-admin {
	background-color: $admin-color-bg-light;
	padding: 30px 0px;
	h1,h2,h3,h4,h5,h6 {
		font-family: $admin-header-font;
	}
	p,li,input,select,textarea {
		font-family: $admin-body-font;
	}
	a {
		color: $admin-color-primary;
		&:hover {
			color: $admin-color-primary;
			text-decoration: underline;
		}
	}
	label {
		font-size: 14px;
		font-weight: 900;
	}
	.breadcrumbs {
		.bc-active {
			margin: 0px;
		}
	}
	.pa-container {
		@include global-width;
		padding-bottom: 60px;
	}
}

.admin-no-results {
	border-top: 1px dashed $admin-color-bg-medium;
	border-bottom: 1px dashed $admin-color-bg-medium;
	text-align: center;
	padding: 30px;
}
.show-input {
	display: block !important;
	width: 100%;
  padding: 8px;
  border: 1px solid $admin-color-bg-medium;
  margin: 5px 0px 10px;
}
.wrong-password {
	span {
		cursor: pointer;
		color: $color-primary;
		&:hover {
			text-decoration: underline;
		}
	}
}

// =========================================/
// Block
// =========================================/

.block {
	background-color: #fff;
	box-shadow: 0px 2px 2px #d3d3d3;
	padding: 40px;
	border-radius: 5px;
	margin-bottom: 50px;
	h1 {
		font-size: 1.5em;
		margin-bottom: 1em;
	}
	p {
		margin-bottom: 1.5em;
	}
	h3 {
		font-size: 1.3em;
		margin-bottom: 1.5em;
	}
	.tabs {
		@include flex-container(stretch,center);
		p {
			margin: 0px;
			padding: 10px 20px;
			border-radius: 3px;
			cursor: pointer;
			flex-grow: 1;
			text-align: center;
			&:hover {
				background-color: #f2f2f2
			}
		}
		.active {
			background-color: $admin-color-secondary !important;
			color: #fff;
		}
	}
	.section {
		margin-bottom: 30px;
		h4 {
			font-size: 1.3em;
			margin-bottom: 15px;
		}
		.desc {
			margin-bottom: 15px;
		}
		input {
			width: 100%;
			padding: 8px;
			border: 1px solid #d3d3d3;
		}
	}
	.section:last-child {
		margin-bottom: 0px;
	}
}

// =========================================/
// Bottom Buttons
// =========================================/

.buttons-bottom {
	@include flex-container(space-between,center);
	position: fixed;
	bottom: 0;
	width: 95%;
	max-width: 1260px;
	padding: 20px;
	background-color: #f9f9f9;
	border-top: 1px solid #d3d3d3;
	div {
		@include flex-container(flex-start,center);
		p {
			margin: 0px 15px 0px 0px;
			input {
				margin-left: 5px;
				width: 225px;
			}
		}
		p:last-child {
			margin: 0px;
		}
		.plain {
			@include transition;
			cursor: pointer;
			&:hover {
				text-decoration: underline;
			}
		}
		.save {
			padding: 5px;
			cursor: pointer;
			background-color: $admin-color-secondary;
			@include transition;
			border-radius: 3px;
			width: 80px;
			text-align: center;
			color: $color-white;
			&:hover {
				background-color: darken($admin-color-secondary,10%);
			}
		}
		.top {
			background-color: #d3d3d3;
			@include flex-container(center,center);
			border-radius: 3px;
			width: 40px;
			height: 40px;
			cursor: pointer;
			@include transition;
			&:hover {
				background-color: #a9a9a9;
			}
			svg {
				width: 20px;
				height: 20px;
			}
		}
	}
}

// =========================================/
// Form 
// =========================================/

.form-details {
	@include flex-container(space-between,flex-start);
	margin-top: 30px;
	.detail {
		width: 49%;
		margin-bottom: 20px;
		h2 {
			font-size: 1.3em;
			font-weight: 500;
			margin-bottom: .5em;
		}
		p {

		}
		input {
			width: 100%;
			padding: 8px;
			border: 1px solid #d3d3d3;
		}
	}
}
.form-groups {
	.group {
		@include flex-container(space-between,center);
		padding: 0px 30px;
		position: relative;
		margin-bottom: 20px;
		.lines {
			position: absolute;
			color: #d3d3d3;
			cursor: move;
			left: 0;
	    font-size: 1.5em;
	    top: 26px;
			&:hover {
				color: gray;
			}
		}
		.delete {
			font-size: 1.5em;
			top: 23px;
			color: #d3d3d3;
			position: absolute;
			padding: 0px 5px;
			cursor: pointer;
			right: 0;
			&:hover {
				color: gray;
			}
		}
		.input {
			background-color: $color-white;
			padding: 20px;
			@include flex-container(flex-start,center);
			border-radius: 5px;
			box-shadow: 2px 2px 2px #d3d3d3;
			position: relative;
			cursor: move;
			p {
				margin: 0px 10px 0px 0px;
		    width: 75px;
		    background-color: #333;
		    color: #fff;
		    border-radius: 3px;
		    font-size: .8em;
		    text-align: center;
			}
			label {
				font-weight: 400;
				margin-right: 5px;
				font-size: 1em;
			}
			input {
				font-size: .9em;
				padding: 5px;
				max-width: 20%;
				margin-right: 10px;
			}
			.required-input {
				margin-right: 0px;
			}
		}
	}
	.group-submit {
		.input {
			cursor: default;
		}
	}
}
.form-buttons {
	@include flex-container(flex-start,center);
	margin-top: 50px;
	p {
		margin: 0px 20px 0px 0px;
		font-weight: 600;
		padding-left: 20px;
	}
	.button {
		padding: 10px;
		cursor: pointer;
		border: 2px dashed #d3d3d3;
		width: 150px;
		border-radius: 5px;
		text-align: center;
		background-color: $color-white;
		margin-right: 15px;
		font-weight: 400;
		&:hover {
			border: 2px solid #d3d3d3;
		}
	}
}

// =========================================/
// Content
// =========================================/

.page-admin {
	.admin-content {
		width: 100%;
		.atbs-header {
			h2 {
				font-size: 2em;
				font-weight: 600;
				margin: 0px 0px 10px 30px;
			}
			.url-header {
				margin: 0px 0px 20px 30px;
				padding: 5px;
				font-size: .85em;
				border-radius: 3px;
				border: 1px solid #d3d3d3;
				background-color: #f9f9f9;
				width: 100%;
				max-width: 400px;
			}
		}
		.ac-block {
			position: relative;
			width: 100%;
			.fa-times {
				position: absolute;
				top: 0;
				right: 0;
				font-size: 1.3em;
				padding: 10px;
				color: #a9a9a9;
				cursor: pointer;
				&:hover {
					color: #333;
				}
			}
			h2 {
				margin-bottom: .5em;
			}
			p {
				font-size: .9em;
				line-height: 1.5em;
				margin-bottom: 1em;
			}
			input {
				width: 100%;
				padding: 8px;
				border: 1px solid #d3d3d3;
			}
			input.uneditable {
				background-color: #f9f9f9;
			}
			.dates-editor {
				@include flex-container;
				input {
					width: 49%;
				}
			}
			.select-images {
				@include transition;
				padding: 5px 15px;
				cursor: pointer;
				background-color: $color-primary;
				color: $color-white;
				border: 0px;
				border-radius: 3px;
				display: inline-block;
				margin: 10px 10px 0px 0px;
				&:hover {
					background-color: darken($color-primary, 5%);
				}
			}
			table {
				width: 100%;
		    margin: 30px 0px 0px;
		    border-radius: 0px;
		    box-shadow: none;
		    table-layout: fixed;
		    tr {
		    	border-top: 1px solid #d3d3d3;
		    	border-bottom: 1px solid #d3d3d3;
		    	border-left: 1px solid #d3d3d3;
		    }
		    td {
		    	background: none;
		    	color: #333;
		    	border-right: 1px solid #d3d3d3;
		    	padding: 10px;
		    }
			}
			.table-resize {
				@include flex-container(flex-start,center);
				margin-top: 20px;
				input {
					width: 40px;
					padding: 4px 8px;
				}
				span {
					font-weight: 600;
					margin: 0px 10px;
				}
				p {
					background-color: gray;
					color: #fff;
					padding: 5px 10px;
					border-radius: 3px;
					margin: 0px 0px 0px 10px;
					&:hover {
						background-color: #a9a9a9;
						cursor: pointer;
					}
				}
			}
		}
		.buttons {
			@include flex-container(space-between, center);
			margin-top: 10px;
			input[type=submit] {
				@include transition;
				padding: 10px 20px;
				cursor: pointer;
				background-color: darken($color-secondary,20%);
				color: $color-white;
				border: 0px;
				border-radius: 3px;
				display: inline-block;
				font-size: 1em;
				line-height: 1em;
				&:hover {
					background-color: darken($color-secondary, 15%);
				}
			}
			.delete {
				padding: 10px 20px;
				font-size: 1em;
				line-height: 1em;
				background-color: #a9a9a9;
				color: #fff;
				border-radius: 3px;
				margin: 0px;
				cursor: pointer;
				&:hover {
					background-color: #333;
				}
			}
		}
		.need-to-save {
			position: fixed;
			top: 0;
			left: 0;
			width: 100vw;
			height: 100vh;
			@include flex-container(center,center);
			background-color: rgba(0,0,0,.5);
			z-index: 1;
			p {
				color: #fff;
				font-size: 2em;
				width: 95%;
				text-align: center;
				max-width: 800px;
				line-height: 1.5em;
			}
		}
	}
}

// =========================================/
// Content - New Section
// =========================================/

.page-admin {
	.admin-content {
		.ac-block {
			.acb-new-section {
				@include flex-container;
				input[type='text'], input[type='number'] {
					width: 49%;
				}
				div {
					width: 100%;
					@include flex-container;
				}
				select {
					width: 49%;
					height: 40px;
					margin: 12px 0px;
					border: 1px solid #d3d3d3;
				}
				.add-section-submit {
					display: inline-block;
					width: max-content;
					background-color: $color-primary;
					transition: all 0.1s;
					padding: 10px 20px;
					cursor: pointer;
					color: #fff;
					border: 0px;
					border-radius: 3px;
					display: inline-block;
					margin: 10px 10px 0px 0px;
					&:hover {
						background-color: darken($color-primary, 5%);
					}
				}
				.list-selected {
					display: block;
					.list-sel {
						position: relative;
						padding: 10px 30px 10px 10px;
						border: 1px solid #d3d3d3;
						width: 500px;
						@include flex-container(space-between,center);
						margin-bottom: 5px;
						p {
							margin: 0px;
						}
						input {
							font-size: .9em;
							padding: 5px;
							width: 44%;
						}
						i {
							cursor: pointer;
							margin-top: 5px;
						}
					}
				}
				.list-options {
					@include flex-container(flex-start,center);
					margin-top: 10px;
					p {
						padding: 8px 20px;
						margin-right: 5px;
						border-radius: 3px;
						background-color: #d3d3d3;
						cursor: pointer;
						&:hover {
							background-color: #c8c8c8;
						}
					}
				}
			}
		}
	}
}

// =========================================/
// Content - Quill Overrides
// =========================================/

.page-admin {
	.admin-content {
		.ac-block {
			.ql-container {
				font-size: inherit;
			}
			.ql-editor {
				padding: 25px 15px 10px;
				.ql-tooltip {
					input {
						width: auto;
					}
				}
				p {
					font-size: .9em;
					line-height: 1.5em;
					margin-bottom: 1em;
				}
				li {
					font-size: .9em;
					line-height: 1.5em;
				}
				h1 {
					font-size: 2.2em;
					line-height: 1.3em;
					margin-bottom: .8em;
					font-weight: 600;
				}
				h2 {
					font-size: 1.8em;
					line-height: 1.3em;
					margin-bottom: .8em;
					font-weight: 600;
				}
				h3 {
					font-size: 1.6em;
					line-height: 1.3em;
					margin-bottom: .8em;
					font-weight: 600;
				}
				h4 {
					font-size: 1.4em;
					line-height: 1.3em;
					margin-bottom: .8em;
					font-weight: 600;
				}
				h5 {
					font-size: 1.2em;
					line-height: 1.3em;
					margin-bottom: .8em;
					font-weight: 600;
				}
				h6 {
					font-size: 1em;
					line-height: 1.3em;
					margin-bottom: .8em;
					font-weight: 600;
				}
				ul,ol {
					padding-left: .5em;
				}
				strong {
					font-weight: 600;
				}
				em {
					font-style: italic;
				}
				sup {
					vertical-align: super;
					font-size: .7em;
					line-height: 1em;
				}
				sub {
					vertical-align: sub;
					font-size: .7em;
					line-height: 1em;
				}
			}
		}
	}
}

// =========================================/
// Footer
// =========================================/

.footer-admin {
	background-color: $admin-color-bg-light;
	padding: 30px 0px;
	display: none;
	.fa-container {
		@include global-width;
		a {
			color: $admin-color-primary;
			&:hover {
				color: $admin-color-secondary;
			}
			i {
				padding-right: 10px;
			}
		}
	}
}

// =========================================/
// Listing
// =========================================/

.admin-content-listing {
	@include flex-container;
	.acl-header {
		width: 100%;
		margin-bottom: 30px;
		a {
			display: inline-block;
			border: 2px solid #d3d3d3;
			padding: 8px 25px;
			color: #333;
			border-radius: 3px;
			text-decoration: none;
			&:hover {
				border: 2px solid #333;
				background-color: #333;
				color: #fff;
			}
		}
	}
	.atb-single {
		width: 32%;
		@include admin-white-background;
		margin: 0px 0px 20px;
		color: $color-black !important;
		text-decoration: none !important;
		@include transition(.2s);
		@include flex-container(space-between,center);
		&:hover {
			box-shadow: 0px 0px 10px #a9a9a9;
		}
		h1,h2 {
			margin: 0px;
			font-size: 24px;
			line-height: 30px;
		}
		p {
			font-size: 14px;
			font-weight: 400;
			margin: 0px;
			font-style: italic;
			color: gray;
			width: 100%;
			margin-top: 10px;
		}
		a {
			display: inline-block;
			border: 2px solid #d3d3d3;
			padding: 5px 15px;
			border-radius: 3px;
			color: #333;
			&:hover {
				background-color: #333;
				color: #fff;
			}
		}
	}
}
.atbs-header .atbsh-links a,
.admin-block-container .cancel-link,
.admin-block-container input[type=submit] {
  margin: 0px;
  border: 2px solid $admin-color-bg-medium;
  padding: 5px 15px;
  border-radius: 3px;
  color: $admin-color-black;
  line-height: 16px;
  margin-left: 3px;
  background: none;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
}
.admin-block-container {
	.cancel-link {
		font-family: $admin-body-font;
	}
	.acul-delete {
		color: #333;
		font-size: 1em;
		&:hover {
			background-color: $admin-color-primary;
			border: 2px solid $admin-color-primary;
			color: $color-white;
		}
	}
}
.atbs-header .atbsh-links a:hover,
.admin-block-container .cancel-link:hover,
.admin-block-container input[type=submit]:hover {
	cursor: pointer;
	background-color: $admin-color-bg;
	border: 2px solid $admin-color-bg;
	color: $admin-color-white;
}

// =========================================/
// Vertical Listing
// =========================================/

.admin-vert-list {
	.avl-post {
		@include admin-white-background;
		display: none;
		h3 {
			font-size: 1.2em;
			font-weight: 600;
			margin-bottom: 15px;
		}
		p:first-child {
			font-weight: 600;
		}
		em {
			font-style: italic;
		}
		p:last-child {
			margin: 0px;
		}
	}
	.avlp-active {
		display: block;
	}
	.avl-admin-post {
		display: block;
		p {
			font-weight: 400 !important;
		}
		div {
			p {
				cursor: pointer;
				&:hover {
					text-decoration: underline;
				}
			}
		}
	}
}

// =========================================/
// Pages
// =========================================/

.admin-block-container {
	.atbs-header {
		margin-bottom: 20px;
		h2 {
			font-size: 1.5em;
			font-weight: 900;
			span {
				padding-left: 10px;
				font-size: .8em;
				font-style: italic;
				font-weight: 500;
			}
		}
	}
	.table-editor {
		border: 1px solid #d3d3d3;
		border-bottom: 0px;
		width: 100%;
		overflow-x: scroll;
		table {
			margin: 0px;
			border: 1px solid #d3d3d3;
			border-bottom: 0px;
			border-right: 0px;
			border-radius: 0px;
			width: 100%;
		}
		tr {
			display: flex;
			margin-top: -2px;
		}
		td {
			background-color: inherit !important;
			border: inherit !important;
			padding: 0px !important;
			margin-left: -2px;
			flex: 1;
			min-width: 200px;
			min-height: 100px;
		}
		textarea {
			border: 1px solid #d3d3d3;
			display: block;
			font-size: 14px;
			padding: 12px;
			width: 100%;
			height: 100%;
		}
	}
}
.table-actions span {
	display: inline-block;
  margin: 10px 10px 0px 10px;
  font-size: .9em;
  color: $admin-color-primary;
  cursor: pointer;
  &:hover {
  	text-decoration: underline;
  }
  i {
  	padding-right: 5px;
  }
}
.ac-block {
	@include admin-white-background;
	h2 {
		font-size: 1.2em;
		font-weight: 900;
		p {
			margin: 10px 0px 20px;
			font-size: 13px;
			font-style: italic;
			font-weight: 400;
		}
	}
	.atbs-editor {
		width: 100%;
		.ql-snow {
			.ql-tooltip.ql-editing input[type=text] {
				width: unset;
			}
		}
	}
	.textarea-inline {
		width: 100%;
    border: 1px solid #a9a9a9;
    height: 80px;
    padding: 15px;
    font-size: .9em;
	}
	.input-inline {
		display: inline-block;
		font-size: .9em;
	}
	.date {
		margin-bottom: 10px;
		span {
			display: inline-block;
			font-weight: 900;
			margin: 0px 10px;
		}
		input {
			padding: 5px;
		}
	}
}
.atbs-footer {
	@include flex-container(flex-end);
	padding: 10px 0px;
}
.atbs-footer form {
	display: inline-block;
}
.ac-block-active {
	.ql-toolbar {
		position: fixed;
		top: 0;
		background-color: #fff;
		z-index: 1;
	}
	.ql-container {
		padding-top: 42px;
	}
}

// =========================================/
// Images
// =========================================/

.acb-image,
.acb-images {
	position: relative;
	.acbi-active {
		height: 250px;
		width: 250px;
		background-size: 90%;
		background-position: center;
		background-repeat: no-repeat;
		background-color: #f2f2f2;
		border-radius: 5px;
		border: 2px solid #f2f2f2;
		position: relative;
		&:hover {
			cursor: pointer;
