// =========================================/
// Media
// =========================================/

.admin-media-container {
	.image-container {
		@include flex-container;
		.acbic-single {
			width: 24%;
			height: 200px;
			margin: 10px 0px;
			position: relative;
			background-position: center;
			background-size: 90%;
			background-repeat: no-repeat;
			border: 2px solid #f2f2f2;
			cursor: pointer;
			border-radius: 3px;
			&:hover {
				border: 2px solid $admin-color-secondary;
			}
			.copy-area {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
			.delete {
				position: absolute;
				top: 5px;
				right: 5px;
				z-index: 1;
				&:hover {
					color: $admin-color-primary;
				}
			}
			.media-file-name {
				@include overlay;
				@include flex-container(center,center);
				margin: 0px;
				font-weight: 600;
			}
		}
	}
	.image-copied-confirmation {
		position: fixed;
		top: 0;
		left: 0;
		width: 100vw;
		height: 100vh;
		@include flex-container(center,center);
		background-color: rgba(0,0,0,.6);
		@include transition(0.5s);
		visibility: hidden;
		opacity: 0;
		z-index: 5;
		p {
			color: #fff;
			font-size: 2em;
			margin: 0px;
		}
	}
	.image-copied-confirmation-active {
		visibility: visible;
		opacity: 1;
	}
}

// =========================================/
// Checkbox
// =========================================/

.checkbox-container {
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-size: 14px;
  font-weight: 400;
  @include flex-container(normal, center);
  margin: 0px;
  height: 25px;
  input {
	  position: absolute;
	  opacity: 0;
	  cursor: pointer;
	  height: 0;
	  width: 0;
	  &:checked ~ .checkmark {
		  background-color: $admin-color-secondary;
		  &:after {
			  display: block;
			}
		}
	}
	.checkmark {
	  position: absolute;
	  top: 0;
	  left: 0;
	  height: 25px;
	  width: 25px;
	  background-color: #d3d3d3;
	  &:after {
		  content: "";
		  position: absolute;
		  display: none;
		  left: 9px;
		  top: 5px;
		  width: 5px;
		  height: 10px;
		  border: solid white;
		  border-width: 0 3px 3px 0;
		  -webkit-transform: rotate(45deg);
		  -ms-transform: rotate(45deg);
		  transform: rotate(45deg);
		}
	}
	&:hover input ~ .checkmark {
	  background-color: #ccc;
	}
}

